import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ExtendedRequestParams } from '@imunify360-api/misc';
import { XHR } from 'app/utils/xhr/xhr';
import { Panel } from 'app/utils/panel';
import { LicenseAPI, Product, LicenseType, Package, License } from '@imunify360-api/license';
import { AuthState } from 'app/services/auth-state';
import { AuthAPI } from '@imunify360-api/auth';
import { ConfigRef } from 'app/core/config-ref';
import { LicenseState } from './license-state';
import { NotificationsService } from 'app/services/notifications';

declare const i360PleskSettings: {
    buyUrl: string,
    marketplace: boolean,
    licenseType: string,
};

@Injectable()
export class LicenseService {
    Product = Product;
    register = this.xhr.request(LicenseAPI.register);
    getToken = this.xhr.rx(AuthAPI.get);

    constructor(
        public xhr: XHR,
        private panel: Panel,
        private router: Router,
        private authState: AuthState,
        private licenseState: LicenseState,
        private notifications: NotificationsService,
        public configRef: ConfigRef,
    ) {}

    get cleanupBlocked() {
        let config = this.configRef._configChange.getValue();
        if (config) {
            return this.authState.isAdmin.value
                && this.licenseState.license.value.product === this.Product.imunifyAV
                && config.PERMISSIONS.upgrade_button;
        }
        return false;
    }

    get cleanupBlockedEndUser() {
        return this.authState.isClient.value
            && this.licenseState.license.value.product === this.Product.imunifyAV;
    }

    registerViaPanel() {
        return this.xhr.post(new ExtendedRequestParams('applyLicenseFromPanel'));
    }

    checkLicense(data: {}): boolean {
        const newLicense = this.licenseState.getLicense(data);

        /**
         * Replace the upgrade URL with URLs with buyUrl from the Plesk Settings if extension installed from the
         * Plesk market
         **/
        if (typeof i360PleskSettings !== 'undefined'
            &&
            i360PleskSettings?.marketplace
            &&
            i360PleskSettings?.buyUrl
        ) {
            newLicense.upgrade_url = i360PleskSettings.buyUrl;
            newLicense.upgrade_url_360 = i360PleskSettings.buyUrl;
        }

        if (!this.licenseState.license.value.isSame(newLicense)) {
            this.licenseState.license.next(newLicense);
        }
        if (this.authState.isClient.value) {
            return true;
        }
        const failed = this.licenseState.license.value.isFailed();
        if (failed && this.authState.isAdmin.value) {
            if (this.router.url !== '/360/admin/license' && this.router.url !== '' && this.router.url !== '/') {
                this.router.navigate(['/', IMUNIFY_PACKAGE, 'admin', 'license']);
            }
            return false;
        } else if (!failed && this.router.url === '/360/admin/license') {
            this.router.navigate(['/']);
        } else if (
            !this.licenseState.license.value.upgradeAvailable
            && this.router.url === '/AV/admin/upgrade'
        ) {
            this.router.navigate(['/']);
        }
        return true;
    }

    private isNeedToShowWarningAfterUpdate(newLicense: License): boolean {
        return !this.panel.isPlesk && this.isLicenseTypeChanged(newLicense.license_type) && !newLicense.hasMessage() && !window['I360_FUNCTIONAL_TEST'];
    }

    private isLicenseTypeChanged(licenseType: LicenseType | undefined): boolean {
        if (!licenseType) return false;

        if (IMUNIFY_PACKAGE === Package.imunify360) {
            return [LicenseType.imunifyAV, LicenseType.imunifyAVPlus].includes(licenseType);
        }

        return [LicenseType.imunify360, LicenseType.imunify360Trial].includes(licenseType);
    }
}
